		font-weight: 700
		position: absolute
		text-transform: uppercase
		top: 50%
		left: 50%
		max-width: 1212px
		width: 63.125vw
		transform: translate(-50%, -50%)
		color: #e6f2f9
		animation: animationTitle 2.2s linear forwards

@media screen and (max-width: 1440px)
